<!-- ***è£å‰ªä¸Šä¼ ï¼ˆå•å¼ ï¼‰*** -->
<template>
  <div id="uploads">
    <el-upload
      v-show="!tailorUrl"
      class="avatar-uploader"
      :style="{ width, height }"
      action="#"
      :multiple="false"
      :show-file-list="false"
      :drag="true"
      :accept="accept"
      :http-request="httpRequest"
    >
      <div class="avatar-uploader-tip">
        <img v-if="cropperUrl" class="avatar-uploader-img" :src="cropperUrl" />
        <slot v-else></slot>
      </div>
    </el-upload>
    <img
      v-show="tailorUrl"
      class="show-img"
      :src="tailorUrl"
      alt=""
      @click="parentClick"
      :style="{ width, height }"
      @drop.prevent.stop="getDropFile"
      @dragover.prevent.stop=""
    />

    <!-- ---------------------------- -->
    <!-- åŸç”Ÿä¸Šä¼ å›¾ç‰‡æ–¹æ³• -->
    <input v-show="false" ref="upload3" :accept="accept" type="file" @change="changeFile" hidden />
    <!-- å¼¹çª— -->
    <el-dialog
      custom-class="custom"
      :visible.sync="dialogShow"
      :before-close="closeDialog"
      :close-on-click-modal="false"
      center
      title="è£å‰ªå›¾æ¡ˆ"
      width="520px"
      :append-to-body="true"
    >
      <div v-if="dialogShow" class="cropper-box">
        <div class="cropper-tailor">
          <span>è£å‰ªåŒº</span>
          <img v-show="false" ref="image" :src="imageUrl" alt="" />
          <span v-if="imageUrl" class="btn" @click="$refs.upload3.click()">é‡æ–°ä¸Šä¼ </span>
        </div>
        <div class="cropper-preview">
          <span>é¢„è§ˆåŒº</span>
          <div class="before" />
        </div>
      </div>
      <el-button @click="closeDialog" size="mini">å–æ¶ˆ</el-button>
      <el-button v-throttle type="primary" size="mini" @click="save">ä¿å­˜</el-button>
    </el-dialog>
  </div>
</template>

<script>
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.css'
import { uploadOSS } from '@/utils/oss/ossUpload.js'
export default {
  props: {
    tailorUrl: String,
    origUrl: String,
    // ä¸Šä¼ æ¡†å¤§å°
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '200px'
    },
    // å›¾ç‰‡å°ºå¯¸
    imgW: {
      type: Number,
      default: 600
    },
    imgH: {
      type: Number,
      default: 600
    },
    // ä¸Šä¼ ç±»å‹
    accept: {
      type: String,
      default: '.jpg,.jpeg,.png,.JPG,.JPEG,.PNG'
    },
    aspectRatio: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      dialogShow: false,
      cropperUrl: '', // è£å‰ªåçš„åœ°å€
      imageUrl: '', // é€‰æ‹©å›¾ç‰‡çš„åœ°å€
      imageName: '', // é€‰æ‹©å›¾ç‰‡çš„åç§°
      myCropper: null, // è£å‰ªå®ä¾‹
      originalFile: null
    }
  },
  created() {},
  mounted() {},
  methods: {
    // æ‹–æ‹½åˆ°å›¾ç‰‡å¼€å¯è£å‰ªæ¡†
    getDropFile(e) {
      this.httpRequest({ file: e.dataTransfer.files[0] })
    },
    // åŸç”Ÿä¸Šä¼ å›è°ƒ
    changeFile(e) {
      console.log(e.target.files, '--')
      this.httpRequest({ file: e.target.files[0] })
    },
    // ä¸Šä¼ è‡ªå®šä¹‰å›è°ƒ
    async httpRequest(option) {
      // æ ¡éªŒå¤§å°
      if (option.file.size > 20 * 1024 * 1024 * 1024) return this.$message.warning('é™åˆ¶20Må›¾ç‰‡')
      // æ ¡éªŒæ ¼å¼
      if (!this.accept.includes(option.file.type.split('/')[1])) return this.$message.warning(`æ ¼å¼é™åˆ¶ï¼š${this.accept}`)
      // æ ¡éªŒå°ºå¯¸
      const reader = new FileReader() // fileè½¬bese64
      const image = new Image()
      reader.readAsDataURL(option.file)
      reader.onload = () => {
        image.src = reader.result
        image.onload = () => {
          // ç¬¦åˆå°ºå¯¸
          if (image.width >= this.imgW && image.height >= this.imgH) {
            this.dialogShow = true // è£å‰ªå¼¹çª—
            this.imageUrl = reader.result // åŸå›¾bese64è·¯å¾„
            this.imageName = option.file.name // åŸå›¾åå­—
            this.originalFile = option.file // åŸå›¾fileæ–‡ä»¶
            if (this.myCropper) {
              this.myCropper.replace(this.imageUrl)
            } else {
              this.init()
            }
          } else {
            this.$message.warning(`è¯·ä¸Šä¼ å°ºå¯¸å¤§äº${this.imgW}*${this.imgH}çš„å›¾ç‰‡`)
          }
        }
      }
    },
    // åˆå§‹åŒ–è£å‰ªå®ä¾‹
    async init() {
      await this.$nextTick()
      this.myCropper = new Cropper(this.$refs.image, {
        viewMode: 1, // è§†å›¾æ¨¡å¼ï¼š 0ï¼šæ²¡æœ‰é™åˆ¶ã€1:è£å‰ªæ¡†åœ¨å›¾ç‰‡å†…ç§»åŠ¨ã€2ï¼šå›¾ç‰‡ä¸å…¨éƒ¨é“ºæ»¡ã€3ï¼šå›¾ç‰‡å¡«å……æ•´ä¸ªè£å‰ªæ¡†
        dragMode: 'move', // æ‹–æ‹½æ¨¡å¼ï¼š â€˜cropâ€™: å¯ä»¥äº§ç”Ÿä¸€ä¸ªæ–°çš„è£å‰ªæ¡†, â€˜moveâ€™: åªå¯ä»¥ç§»åŠ¨,  â€˜noneâ€™: ä»€ä¹ˆä¹Ÿä¸å¤„ç†
        preview: '.before', // é¢„è§ˆå¼¹çª—
        background: true, // æ˜¾ç¤ºç½‘æ ¼èƒŒæ™¯  //trueï¼Œæ˜¾ç¤ºé©¬èµ›å…‹èƒŒæ™¯ï¼Œfalseæ—¶æ˜¾ç¤ºç°è‰²èƒŒæ™¯è‰²
        aspectRatio: this.aspectRatio, // è£å‰ªæ¡†æ¯”ä¾‹ 16/9
        rotatable: true
        // autoCropArea: 1, // è£å‰ªæ¡†æ’‘æ»¡
        // zoomOnWheel: true
      })
    },
    // è£å‰ª--ç¡®å®š
    async save() {
      // ä¸åŒè£å‰ªå°ºå¯¸ä¸åŒå¤§å°
      const arr = [
        { width: 1600, height: 900 },
        { width: 800, height: 600 },
        { width: 600, height: 600 }
      ]
      const arrIndex = this.aspectRatio > 1.5 ? 0 : this.aspectRatio > 1 ? 1 : 2

      // è®¾ç½®è£å‰ªåçš„canvaså¤§å°  // å°†canvasæ ‡ç­¾è½¬base64url
      this.cropperUrl = this.myCropper
        .getCroppedCanvas({
          minWidth: arr[arrIndex].width, // è£å‰ªåçš„é•¿å®½
          minHeight: arr[arrIndex].height
        })
        .toDataURL('image/png')

      // base64urlè½¬fileæ–‡ä»¶
      const tailorFile = this.dataURLtoBlob(this.cropperUrl)

      console.log(this.originalFile, 'è£å‰ªå‰çš„æ–‡ä»¶')
      console.log(tailorFile, 'è£å‰ªåçš„æ–‡ä»¶')
      // ä¸Šä¼ oss
      const origUrl = await uploadOSS(this.originalFile)
      const tailUrl = await uploadOSS(tailorFile)
      this.$emit('update:origUrl', origUrl.url)
      this.$emit('update:tailorUrl', tailUrl.url)

      this.closeDialog()
    },
    closeDialog() {
      this.dialogShow = false
      this.myCropper = null
    },
    //
    parentClick() {
      this.dialogShow = true
      this.cropperUrl = this.origUrl
      this.init()
    },

    //
    // å°†base64è½¬æ¢ä¸ºblob
    dataURLtoBlob(dataurl) {
      const arr = dataurl.split(',')
      const mime = arr[0].match(/:(.*?);/)[1]
      const bstr = atob(arr[1])
      let n = bstr.length
      const u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      const blob = new Blob([u8arr], { type: mime })
      return this.blobToFile(blob)
    },
    // å°†blobè½¬æ¢ä¸ºfile
    blobToFile(theBlob) {
      theBlob.lastModifiedDate = new Date()
      theBlob.name = this.imageName
      return theBlob
    }
  }
}
</script>

<style lang="less">
#uploads {
  overflow: hidden;
  margin-right: 20px;

  .avatar-uploader {
    float: left;

    .el-upload {
      overflow: hidden;
      width: 100%;
      height: 100%;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      box-sizing: border-box;

      .el-upload-dragger {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    .el-upload:hover {
      border-color: #409eff;
    }

    .avatar-uploader-tip {
      i {
        font-size: 28px;
        color: #8c939d;
        font-weight: bolder;
      }

      p {
        color: #999;
        font-size: 14px;
      }

      .avatar-uploader-img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }

  .show-img {
    border: 1px dashed #d9d9d9;
    cursor: pointer;
    border-radius: 6px;

    &:hover {
      border-color: #2858f8;
    }
  }
}

.cropper-box {
  display: flex;
  justify-content: space-between;
  height: 360px;

  .cropper-tailor {
    /* è£å‰ªå›¾ç‰‡ */
    width: 280px;
    height: 280px;
    border-radius: 4px;

    .btn {
      display: block;
      width: 100%;
      height: 36px;
      margin-top: 10px;
      border: 1px solid #849ff9;
      font-size: 14px;
      color: #2858f8;
      line-height: 36px;
      text-align: center;
      border-radius: 4px;
      cursor: pointer;
      box-sizing: border-box;
    }
  }

  .cropper-preview {
    .before {
      width: 160px;
      height: 160px;
      border: 1px solid #d9d9d9;
      overflow: hidden;
      /* è¿™ä¸ªå±æ€§å¯ä»¥å¾—åˆ°æƒ³è¦çš„æ•ˆæœ */
    }
  }
}
</style>
