<!-- **åŸç”Ÿä¸Šä¼ ** -->
<template>
  <div>
    <!-- å¤šé€‰å±•ç¤º -->
    <template v-if="origListUrl.length">
      <div v-for="(item, index) in origListUrl" :key="item.base64Url" class="avatar-uploader-li" :style="{ width, height }">
        <img :src="item.base64Url" />
        <i class="el-icon-error" @click="origListUrl.splice(index, 1)"></i>
      </div>
    </template>
    <div class="upload2" :style="{ width, height }" @click="openFile" @drop.prevent.stop="getDropFile" @dragover.prevent.stop="">
      <img v-if="origUrl" :src="origUrl" />
      <slot v-else></slot>
    </div>

    <!-- åŸç”Ÿè°ƒç”¨ä¸Šä¼ æ–¹æ³• -->
    <input ref="upload" :accept="accept" :multiple="multiple" type="file" @change="changeFile" hidden style="display: none" />
  </div>
</template>

<script>
import { uploadOSS } from '@/utils/oss/ossUpload.js'
export default {
  props: {
    origUrl: String,
    origListUrl: {
      type: Array,
      default: () => []
    },
    // æ˜¯å¦å¤šé€‰
    multiple: {
      type: Boolean,
      default: false
    },
    // ä¸Šä¼ æ¡†å¤§å°
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '200px'
    },
    // å›¾ç‰‡å°ºå¯¸
    imgW: {
      type: Number,
      default: 600
    },
    imgH: {
      type: Number,
      default: 600
    },
    // ä¸Šä¼ ç±»å‹
    accept: {
      type: String,
      default: '.jpg,.jpeg,.png,.JPG,.JPEG,.PNG'
    }
  },
  data() {
    return {
      fileList: [],
      arr: []
    }
  },
  created() {},
  mounted() {},
  methods: {
    // ç‚¹å‡»è°ƒç”¨åŸç”Ÿé€‰æ‹©å›¾ç‰‡å¼¹çª—
    openFile() {
      console.log(this.$refs.upload)
      this.$refs.upload.click()
    },
    // ç‚¹å‡»ä¸Šä¼ è§¦å‘
    changeFile(e) {
      this.verify(e.target.files)
    },
    // æ‹–æ‹½ä¸Šä¼ è§¦å‘
    getDropFile(e) {
      this.verify(e.dataTransfer.files)
    },
    verify(files) {
      if (!files.length) return false
      this.multiple ? this.fileListFilter(files) : this.fileFilter(files)
    },

    // å•å¼ è¿‡æ»¤
    async fileFilter(data) {
      const file = data[0]
      // æ ¡éªŒå¤§å°
      if (file.size > 20 * 1024 * 1024 * 1024) return this.$message.warning('é™åˆ¶20Må›¾ç‰‡')
      // æ ¡éªŒæ ¼å¼
      if (!this.accept.includes(file.type.split('/')[1])) return this.$message.warning(`æ ¼å¼é™åˆ¶ï¼š${this.accept}`)
      // æ ¡éªŒå°ºå¯¸
      const url = URL.createObjectURL(file) // å›¾ç‰‡base64åœ°å€
      const { w, h } = await this.getImgWH(url) // å›¾ç‰‡å®½é«˜
      if (w < this.imgW && h < this.imgH) return this.$message.warning(`è¯·ä¸Šä¼ å°ºå¯¸å¤§äº${this.imgW}*${this.imgH}çš„å›¾ç‰‡`)
      // ä¸Šä¼ oss
      const datas = await uploadOSS(file)
      this.$emit('update:origUrl', datas.url)
    },
    // å¤šå¼ è¿‡æ»¤
    async fileListFilter(files) {
      if (files.length >= 20) return this.$message.warning('å•æ¬¡ä¸Šä¼ å›¾ç‰‡ä¸èƒ½å¤§äº20å¼ ')

      let hint = false
      for (var i = 0; i < files.length; i++) {
        // æ ¡éªŒå›¾ç‰‡å¤§å°
        if (files[i].size > 20 * 1024 * 1024 * 1024) {
          hint = true
          continue
        }
        // æ£€éªŒç±»å‹
        if (!this.accept.includes(files[i].type.split('/')[1])) {
          hint = true
          continue
        }
        // æ ¡éªŒå°ºå¯¸
        const url = URL.createObjectURL(files[i])
        const { w, h } = await this.getImgWH(url)
        if (w >= this.imgW && h >= this.imgH) {
          this.fileDispose(files[i])
        } else {
          hint = true
        }
      }
      if (hint) {
        this.$message.warning('å·²è¿‡æ»¤éå›¾ç‰‡ç±»å‹æ–‡ä»¶æˆ–å•å¼ å¤§äº20Mçš„å›¾ç‰‡')
      }
    },

    // ä¸Šä¼ oss
    async fileDispose(file) {
      // const url = URL.createObjectURL(file)
      // const data = await this.getImgWH(url)
      this.fileList.push({
        name: file.name,
        base64Url: URL.createObjectURL(file)
        // ossUrl: uploadOSS(file)
        // width: data.w,
        // height: data.h
      })
      this.$emit('update:origListUrl', this.fileList)

      uploadOSS(file).then(res => {
        console.log(res, '==')
        this.arr.push(res)
        console.log(this.arr)
      })
    },
    // è·å–å›¾ç‰‡åˆ†è¾¨ç‡å¤§å°
    getImgWH(imgUrl) {
      return new Promise(resolve => {
        const image = new Image()
        image.src = imgUrl
        image.onload = function () {
          const w = image.width
          const h = image.height
          resolve({ w, h })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.upload2 {
  // float: left;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  box-sizing: border-box;
  div {
    text-align: center;
    cursor: pointer;
    i {
      font-size: 28px;
      color: #8c939d;
      font-weight: bolder;
    }
    p {
      color: #999;
      font-size: 14px;
    }
  }
  &:hover {
    border-color: #409eff;
  }
  img {
    width: 100%;
    height: 100%;
  }
}
.avatar-uploader-li {
  position: relative;
  float: left;
  margin-right: 20px;
  margin-bottom: 20px;
  &:hover {
    .el-icon-error {
      display: block;
    }
  }
  img {
    display: block;
    width: 100%;
    height: 100%;
    border: 1px solid #d9d9d9;
    border-radius: 6px;
  }
  .el-icon-error {
    display: none;
    position: absolute;
    top: -4px;
    right: -8px;
    font-size: 18px;
    cursor: pointer;
    &:hover {
      color: #409eff;
    }
  }
}
</style>
