const axios = require('axios')

let policyData = null

export async function uploadOSS(file) {
  if (!policyData) {
    await genOssData()
  }
  return gtOSSurl(file)
}

// è·å–ossé˜¿é‡Œäº‘ä¿¡æ¯
async function genOssData() {
  const res = await axios.post('http://192.168.1.97:5008/Basic/Support/GenPostPolicy', {
    data: {
      module: 'CUMS',
      upfilePath: 'OA'
    },
    sign: 'B99DD86F2011A98622A37F472570B5C4',
    sysName: 'Front_CUMS',
    ts: '1647934436000',
    version: '1.0.0'
  })
  policyData = res.data.data
}

function gtOSSurl(file) {
  return new Promise((resolve, reject) => {
    const nfile = file
    nfile.timestamp = new Date().getTime().toString() + Math.floor(Math.random() * 100000) // ç»™å›¾ç‰‡æ·»åŠ ä¸€ä¸ªæ—¶é—´æˆ³
    const formData = new FormData() // æ³¨æ„formDataé‡Œappendæ·»åŠ çš„é”®çš„å¤§å°å†™
    formData.append('key', calculateObjectName(nfile)) // å­˜å‚¨åœ¨ossçš„æ–‡ä»¶è·¯å¾„
    formData.append('OSSAccessKeyId', policyData.accessId) // accessKeyId
    formData.append('policy', policyData.policy) // policy
    formData.append('Signature', policyData.signAture) // ç­¾å
    // // å¦‚æœæ˜¯base64æ–‡ä»¶ï¼Œé‚£ä¹ˆç›´æ¥æŠŠbase64å­—ç¬¦ä¸²è½¬æˆblobå¯¹è±¡è¿›è¡Œä¸Šä¼ å°±å¯ä»¥äº†
    formData.append('file', nfile)
    formData.append('success_action_status', 200) // æˆåŠŸåè¿”å›çš„æ“ä½œç 

    axios.post(policyData.host, formData).then(
      res => {
        if (res.status === 200) {
          // ä¸è£å‰ªï¼ŒåŸå›¾å’Œè£å‰ªå›¾ä¸€æ ·
          const data = {
            url: 'http://tu.chdesign.cn/' + calculateObjectName(nfile) // åŸå›¾
          }
          resolve(data)
        }
      },
      err => {
        reject(err)
      }
    )
  })
}

//
function calculateObjectName(thefile) {
  const suffix = getSuffix(thefile.type, thefile.name)
  const name = policyData.dir + thefile.timestamp + '.' + suffix
  return name
}
function getSuffix(type, name) {
  const pos = type.lastIndexOf('/')
  let suffix = ''
  if (pos !== -1) {
    suffix = type.substring(pos + 1)
  }
  if (!type) {
    suffix = name.split('.')[1]
  }
  return suffix
}
