<template>
  <!-- element-uiä¸Šä¼  -->
  <div id="uploads">
    <!-- å¤šé€‰å±•ç¤º -->
    <template v-if="imageUrlList.length">
      <div v-for="i in imageUrlList" :key="i" class="avatar-uploader-li" :style="{ width, height }">
        <img :src="i" />
      </div>
    </template>
    <!-- å•é€‰å±•ç¤º -->
    <el-upload
      class="avatar-uploader"
      :style="{ width, height }"
      action="#"
      :multiple="multiple"
      :show-file-list="false"
      :drag="true"
      :accept="accept"
      :http-request="httpRequest"
    >
      <div class="avatar-uploader-tip">
        <img v-if="imageUrl" :src="imageUrl" class="avatar-uploader-img" />
        <slot v-else></slot>
      </div>
    </el-upload>
  </div>
</template>

<script>
export default {
  props: {
    // æ˜¯å¦å¤šé€‰
    multiple: {
      type: Boolean,
      default: false
    },
    // ä¸Šä¼ æ¡†å¤§å°
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '200px'
    },
    // å›¾ç‰‡å°ºå¯¸
    imgW: {
      type: Number,
      default: 600
    },
    imgH: {
      type: Number,
      default: 600
    },
    // ä¸Šä¼ ç±»å‹
    accept: {
      type: String,
      default: '.jpg,.jpeg,.png,.JPG,.JPEG,.PNG'
    }
  },
  data() {
    return {
      imageUrl: '',
      imageUrlList: []
    }
  },
  created() {},
  mounted() {},
  methods: {
    //
    // onChage(file, fileList) {
    //   console.log(111)
    //   // console.log(fileList)
    //   if (fileList.length >= 50) {
    //     this.$message.warning('å•æ¬¡ä¸Šä¼ å›¾ç‰‡ä¸èƒ½å¤§äº20å¼ ')
    //     return false
    //   }
    // },
    // ä¸Šä¼ è‡ªå®šä¹‰å›è°ƒ
    httpRequest(option) {
      console.log(option)
      // å•é€‰å…ˆç½®ç©º
      if (!this.multiple) {
        this.imageUrlList = []
      }
      // ç¬¦åˆæ ¼å¼
      if (this.accept.includes(option.file.type.split('/')[1])) {
        console.log(1111)
        const reader = new FileReader() // fileè½¬bese64
        const image = new Image()

        reader.readAsDataURL(option.file)
        reader.onload = () => {
          image.src = reader.result
          image.onload = () => {
            // console.log('ä¸Šä¼ çš„å›¾ç‰‡å°ºå¯¸ï¼š', image.width, image.height)
            if (this.multiple) {
              this.imageUrlList.push(reader.result)
              this.$emit('getUrlList', this.imageUrlList)
            } else {
              this.imageUrl = reader.result
            }
          }
        }
      } else {
        this.$message.error('å·²è¿‡æ»¤éå›¾ç‰‡ç±»å‹æ–‡ä»¶æˆ–å•å¼ å¤§äº20Mçš„å›¾ç‰‡')
      }
    }
  }
}
</script>

<style lang="less">
#uploads {
  overflow: hidden;
  margin-right: 20px;
  .avatar-uploader {
    float: left;
    .el-upload {
      overflow: hidden;
      width: 100%;
      height: 100%;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      box-sizing: border-box;
      .el-upload-dragger {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
    .el-upload:hover {
      border-color: #409eff;
    }
    .avatar-uploader-tip {
      i {
