      }
      p {
        color: #999;
        font-size: 14px;
      }
      .avatar-uploader-img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }
  // å¤šå¼ æ ·å¼

  .avatar-uploader-li {
    float: left;
    margin-right: 20px;
    margin-bottom: 20px;
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}
</style>
